import React from 'react'
const articles = JSON.parse(localStorage.getItem('articlesKey'));

const Accueil = () => {
    return (
<>


<div id="accueil"style={{height:"500px"}} className=" row  col-12">  
    <div className=" col-2 mb-5 offset-2 " style={{marginBottom:"200px"}}> 
        <img src="images/ii.png" width="350px" className=" mt-5 d-inline " />
    </div>
    <div className="  col-6"> 
    <h1 className="  display-1 mt-5 offset-3 col-12"style={{fontFamily: "serif"}}>
            NETArticles! <br/> LE MEILLEUR DE L'INFO EN UN CLICK.</h1>
    </div> 
</div>



    <div id="accueil"style={{backgroundImage:"url(images/c.jpg)",height:"500px"}} className="">  
        <h1 className="text-white offset-2 display-1 mt-5"style={{fontFamily: "serif"}}>LA BIG DATA !
        <p>Blockchain et Big Data, quels enjeux stratégiques ?</p> </h1>        
    </div>

    <div id="accueil"style={{backgroundImage:"url(images/cc.jpg)",height:"500px"}} className="">  
        <h1 className="text-white offset-2 display-1 "style={{fontFamily: "serif"}}>Rédaction web :
        <p>Les 10 étapes pour rédiger un contenu de qualité</p> </h1>        
    </div>

    <div id="accueil"style={{backgroundImage:"url(images/ee.jpg)",height:"1200px"}} className="">  
        <h1 className="text-white offset-2 display-1 "style={{fontFamily: "serif"}}>Qu’est-ce que la cybersécurité ?
        <p>PROTÉGEZ VOS CYBER-RESSOURCES ET DONNÉES CRITIQUES</p> </h1>        
    

    
            
        <div className="container mt-5" style={{boxShadow:"10px 15px 10px 10px rgba(0, 0, 0, 0.2)"}}>
            <div className="bg-light p-5 mt-5" >
                <div className="container text-center">
                    <h1 className="display-4">Actualité </h1>
                </div>
                    <div className="row row-cols-1 row-cols-md-2 g-4 mt-5">
                {    articles.map((article,index)=>{
                        return <div key={index} className="col">
                        <div className="card mb-3" style={{width: "540px"}}>
                            <div className="row g-0">
                                <div className="col-md-4">
                                <img src={`images/${article.image}`} className="card-img-top" alt="..."/>
                                </div>
                                <div className="col-md-8">
                                    <div className="card-body">
                                    <h5 className="card-title">{article.titre}</h5>
                                    <p className="card-text"><small className="text-muted">{article.date}</small></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                })
            }</div>
        </div>
    </div>
</div>

   


</>
    )
}

export default Accueil

