import React, { Component } from 'react';
import Tableau from './Tableau';
import Ajout from './Ajout';
import EditA from './Edit';



class Admin extends Component {
    constructor(props) {
        super(props);
        this.state = { 
            articles:[
                    {
                    id:1,
                    titre:"Comment se prémunir contre les rançongiciels",
                    auteur:'Alexandre Léchenet ',
                    date:'05/11/2020', 
                    text:'De nombreuses collectivités ont été attaquées par des rançongiciels ces derniers mois, immobilisant parfois leurs services pendant plusieurs semaines. Des entités de toutes tailles ont été ciblées, montrant l’importance de prendre en compte ce risque d’attaque à tous les échelons. Outre une hygiène numérique primordiale et un soin quotidien à la sécurité informatique avec un budget conséquent, l’anticipation en cas d’attaque est importante.', 
                    image:'cs.jpg'
                    },
                    {
                    id:2,
                    titre:"La smartcity, comment ses composantes vont influer sur la ville du futur",
                    auteur:'Auteur partenaire',
                    date:'15/12/2020 ',
                    text:'La digitalisation accrue des usages, la mobilisation des collectivités et des industriels ont donné un sérieux coup d’accélérateur à la ville du futur. Du vaste concept associé aux seules métropoles, la smartcity et ses solutions durables se concrétisent sur l’ensemble des territoires.',
                    image:'sm.jpg'
                    },
                    {
                    id:3,
                    titre:"Rançongiciels : la liste des collectivités victimes s’allongent, comme une litanie",
                    auteur:'Alexandre Léchenet',
                    date:'08/10/2020',
                    text:'Des données dérobées à la ville de Mitry-Mory ont été publiées en ligne mardi 6 octobre. Les annonces de collectivités touchées par les rançongiciels se multiplient, les pirates informatiques ayant su tirer profit de la crise sanitaire.',
                    image:'h.jpg'
                    },
                    {
                    id:4,
                    titre:"Comment Adico accompagne les collectivités dans le numérique",
                    auteur:'LISE VERBEKE',
                    date:' 07/10/2020',
                    text:'L’Association pour le développement et l’innovation numérique des collectivités compte 60 salariés qui accompagnent les collectivités sur les sujets de cybersécurité, site web, gestions des data, et plus généralement dans l’usage des outils informatiques et l’innovation.',
                    image:'d.jpg'
                    },
                ],
                formF:false,
                indexM:-1,
                editArt:{id:-1,titre:'',auteur:'',date:'',text:'',image:''},
         }
    }
componentDidMount=()=>{

    let article1 = JSON.parse(localStorage.getItem('articlesKey'));
    if(!article1 || article1.length === 0){
        localStorage.setItem('articlesKey',JSON.stringify(this.state.articles));
        let articlesRecup = JSON.parse(localStorage.getItem('articlesKey'));
        this.setState({articles:articlesRecup})
    }else{

        let article2 = JSON.parse(localStorage.getItem('articlesKey'));
        this.setState({articles:article2})
        console.log(article2)

    }

}
handleSupp=(index)=>{
    let newArticles = [...this.state.articles]
    const article = newArticles.filter((art,id)=>{
        return id !== index;
    })
    this.setState({articles:article},()=>{
    localStorage.setItem('articlesKey',JSON.stringify(this.state.articles));
})


}


handleAff=(etat)=>{
    this.setState({formF:true})
}



handleArticle=(article)=>{
    article.id = this.state.articles[this.state.articles.length -1].id+1
    let articlesClone = [...this.state.articles,article]
    this.setState({articles:articlesClone},()=>{
    localStorage.setItem('articlesKey',JSON.stringify(this.state.articles));
})

}

handleED=(index)=>{

    const EditA = this.state.articles[index];
    this.setState({editArt:EditA,indexM:index})
    console.log(this.state.editArt)
    console.log(this.state.indexM)


}


handleUpdate=(data)=>{

    let articlesUpdate = [...this.state.articles]
    articlesUpdate[this.state.indexM] = data;
    this.setState({articles:articlesUpdate},()=>{
        localStorage.setItem('articlesKey',JSON.stringify(this.state.articles));
    })
    console.log(this.state.indexM)
}

    render() { 
console.log(localStorage.getItem('articlesKey'))
 
        return ( 
            <> 

                    {
                        (!this.state.formF && this.state.indexM === -1) 
                        && <button onClick={this.handleAff} className="btn btn-success offset-11 mt-5">ajouter</button>
                    }

                    {
                        (!this.state.formF && this.state.indexM > -1)
                        ?<EditA Edition={this.state.editArt} update={this.handleUpdate} />  
                        :
                        (this.state.formF)
                        ?<Ajout getArt={this.handleArticle} />
                        :<Tableau listes={this.state.articles} handleDelete={this.handleSupp} handleEdit={this.handleED} />
                    }
            </>
         );
    }
}
 
export default Admin;