import {BrowserRouter,Switch,Route,Redirect,NavLink} from 'react-router-dom'
import 'bootstrap/dist/css/bootstrap.css'
import 'popper.js'
import Admin from './Admin';
import { FaBookReader,FaHome,FaUserLock,FaUserTimes  } from 'react-icons/fa';
import Accueil from './pages/Accueil';
import Articles from './pages/Articles';
import Presentation from './pages/Presentation';
import Contact from './pages/Contact';
import Error from './pages/Error';
import Login from './Login';
import Logout from './Logout';


const authentificate =()=>{

const d =new Date();
  const test = d.toLocaleDateString();
    if(test === sessionStorage.getItem('token')){
      return true;
    }else{
      return false;
    }
}

const Menu = () => {
  return (

<>

<BrowserRouter basename="/monBlog">
 
<nav className="navbar navbar-expand-lg navbar-dark ">
      <div className="container-fluid">
              <ul className="navbar-nav me-auto mb-2 mb-lg-3 col-3  mt-5 ">
                    <li  className="nav-item col-3 ">
                    <NavLink className="nav-link text-center"   aria-current="page" to="/"exact>  ACCUEIL </NavLink >
                    </li>
                    <li  className="nav-item col-3 ">
                    <NavLink className="nav-link text-center"   to="/articles">  ARTICLES </NavLink >
                    </li>
                    <li  className="nav-item col-5 text-center">
                    <NavLink className="nav-link"   to="/apropos">PRESENTATION</NavLink >
                    </li>
                    <li  className="nav-item col-4 text-center">
                    <NavLink className="nav-link"   to="/services">CONTACT</NavLink >
                    </li>
                    <li  className="  offset-5 ">
                    <NavLink className=" navbar-brand  " to="/" ><p className="bd display-4 ">N<img src="images/ii.png"  width="60px"/>TArticles </p> <p className="lead text-center">L'informatique simplement !</p></NavLink>
                    </li>
                    {/* <FaBookReader/> */}
              </ul>
          <span className="navbar-text mb-5">
            {!authentificate()
            ?<NavLink className=" mp nav-link" activeClasseName="active"  to="/login"><FaUserLock /> Connexion</NavLink >
            :<NavLink className=" mp nav-link" activeClasseName="active"  to="/logout"><FaUserTimes />  Deconnexion</NavLink >
            }
          </span>
      </div>
</nav>
s        <Switch>
              <Route path='/' component={Accueil} exact/>
              <Route path='/articles' component={Articles} exact/>
              <Route path='/apropos' component={Presentation} />
              <Route path='/services' component={Contact} />
              <Route path='/login' component={Login} />
              <Route path='/logout' component={Logout} />
              <Redirect  from='/admin123/reload' to="/admin123" />
              <Route path='/admin123' render={()=>(
                  authentificate()
                  ?(<Admin />)
                  :(<Redirect to="/login"/>)
              )}  />
              <Route path='*' component={Error} />    
        </Switch>
 </BrowserRouter>


</>
 );
}

export default Menu;
