import React from 'react'
import { FaPaintBrush, FaTrash } from "react-icons/fa";
import {toast,ToastContainer} from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const Tableau = (props) => {
    console.log(props)
    return (
            <> 
<div className="container">
    <table className="table border m-2 text-center table-striped ">
        <thead className="bg-warning">
            <tr>
                <th>Id</th><th>titre</th><th>auteur</th><th>date</th><th>image</th><th>text</th><th colSpan="2">Action</th>
            </tr>
        </thead>
        <tbody>
    {props.listes.map((list,index)=>{
    return (
        <tr key={index} >
            <td>{list.id}</td>
            <td>{list.titre}</td>
            <td>{list.auteur}</td>
            <td>{list.date}</td>
            <td><img src={`images/${list.image}`} width="80px" alt="pic"/></td>
            <td>{list.text}</td>
            <td><button className="btn btn-warning" onClick={()=>{props.handleEdit(index)}}><FaPaintBrush /></button></td>
            <td><button className="btn btn-danger" onClick={()=>{
                props.handleDelete(index)
                if(window.confirm('Etes vous sûr de supprimer cette voiture')){
                    {props.handleDelete(index)}
                    toast('Vroum-vroum supprimé avec succès',  {className: 'bg-info text-white fw-bold'})
                }  
                }}><FaTrash /></button></td></tr>    
            )

    })}
        </tbody>
    </table>  
        <ToastContainer
                        position="top-right"
                        autoClose={2000}
                        hideProgressBar={false}
                        newestOnTop={false}
                        closeOnClick
                        rtl={false}
                        pauseOnFocusLoss
                        draggable
                        pauseOnHover
        
        />   
</div>
</>
     
    )}

export default Tableau
