import React, { Component } from 'react';
import {FaLongArrowAltLeft, FaRegHandPointLeft } from "react-icons/fa";
import {Link} from 'react-router-dom'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

class EditA extends Component {
    constructor(props) {
        super(props);
        this.initiateState = { 
            titre:"",
            auteur:"",
            date:"",
            image:"",
            text:""
         }
         this.state = this.initiateState;
    }

    
componentDidMount=()=>{
    this.setState(this.props.Edition)
}

componentDidUpdate=()=>{
    const id = this.props.Edition.id;
    if(id !== this.state.id){ 
        this.setState(this.props.Edition)
    }

}

handelChange=(e)=>{
    const{name,value} = e.target
    const Val = value;
    this.setState({[name]:Val})
}

handleSubmit=(e)=>{
    e.preventDefault();
    let img='';
    if(this.state.image){
            img = this.state.image.match(/[a-zA-Z0-9-_]+\.(jpg|png)/)[0]
    }
    const updateA = {
        titre : this.state.titre,
        auteur : this.state.auteur,
        date : this.state.date,
        image : img,
        text : this.state.text,
    }
    this.props.update(updateA);
    this.setState(this.initiateState);

    toast('Article Ajoutée avec succes !',{className:'bg-success text-white fw-bolder'});
    
}

render() { 

        const{titre,auteur,date,text}=this.state;
    
            return ( 
                
    
<>
<div className="container col-10"> 
        <div className="card">         
            <div className="card-header text-center">         
                <h3>Edition ARTICLE</h3>
            </div>
                <div className="card-body"> 
                    <form className="row g-3 " onSubmit={this.handleSubmit}>
                        <div className="col-md-6">
                            <label htmlFor="titre" className="form-label">Titre</label>
                            <input type="text"onChange={this.handelChange} value={titre} className="form-control" id="titre" name="titre" placeholder="Le Titre" required  />
                            </div>
                            <div className="col-md-6">
                                <label htmlFor="auteur" className="form-label">Auteur</label>
                                <input type="text" onChange={this.handelChange} value={auteur} className="form-control" id="auteur" name="auteur"  placeholder="L'Auteur" required  />
                            </div>
                            <div className="col-md-6">
                                <label htmlFor="la Date" className="form-label">La Date</label>
                                <input type="text" onChange={this.handelChange} value={date} className="form-control" id="date" name="date" placeholder="Entrer le pays" required  />
                            </div>
                            <div className="col-md-6">
                                <label htmlFor="image" className="form-label">Image</label>
                                <input type="file"onChange={this.handelChange}  className="form-control" id="image" name="image"  />
                            </div>
                            <div className="col-md-12">
                                <label htmlFor="le text de l'article" className="form-label">l'Article</label>
                                <textarea type="text" onChange={this.handelChange} value={text} className="form-control" id="text" name="text" placeholder="Ecrivez votre Article" required  />
                            </div>
                            <div className="offset-5 col-12">
                                <button className="btn btn-primary" type="submit">Publier</button>
                            </div>
                    </form>     
                </div>  
            </div>     
    <Link to="/admin123/reload"><FaLongArrowAltLeft size="95"/></Link>
</div>  
    <ToastContainer
                             position="top-right"
                             autoClose={2000}
                             hideProgressBar={false}
                             newestOnTop={false}
                             closeOnClick
                             rtl={false}
                             pauseOnFocusLoss
                             draggable
                             pauseOnHover/> 
 
                </>
             );
        }
}
 
export default EditA;