import React from 'react'

const Articles = () => {
    const articles = JSON.parse(localStorage.getItem('articlesKey'));
    return (
        <>
        <div className="container mt-5" style={{boxShadow:"10px 15px 10px 10px rgba(0, 0, 0, 0.2)"}}>
        <div className="bg-light p-5">
            <div className="container text-center">
                 <h1 className="display-4">NOS ARTICLES </h1>
                 <p className="lead">L'actualité informatique et hight-tech pour décideurs IT !</p>
            </div>
        

            <div className="row row-cols-1 row-cols-md-2 g-4 mt-5">
                
        {
            articles.map((article,index)=>{
                return <div key={index} className="col">
                
                <div className="card">
                    <img src={`images/${article.image}`} className="card-img-top" alt="..."/>
                    <div className="card-body">
                        <h5 className="card-title text-center"> {article.titre}</h5>
                            <ul className="list-group list-group-flush">
                                <li className="list-group-item"> {article.text}</li>
                                <li className="list-group-item">Date : {article.date}</li>
                                <li className="list-group-item">Auteur : {article.auteur}</li>
                            </ul>
                    </div>
                </div>
            </div>
                    
                })
            }
                
            </div>
    </div>
</div>
        </>
    )
}

export default Articles
